import { IShopSettings } from "../../Models/Interfaces/shop-settings.interface";
import { ZenIndiaShoppeActions, ZenIndiaShoppeActionTypes } from "./zen-india-shoppe.actions";

export const ZenIndiaShoppeFeatureKey = 'ZenIndiaShoppe';

export interface IShopSettingsState {
    data?: IShopSettings,
    loading: Boolean,
    loaded: Boolean,
    isError: Boolean,
    error?: any
}

const initialState: IShopSettingsState = {
    data: undefined,
    loaded: false,
    loading: false,
    isError: false,
    error: undefined
};

export function ZenIndiaShoppeReducer(state = initialState, action: ZenIndiaShoppeActions) {
    switch (action.type) {
        case ZenIndiaShoppeActionTypes.SetUpShop:
            return {
                ...state,
                loading: true,
                loaded: false,
                isError: false,
                error: undefined
            };
        case ZenIndiaShoppeActionTypes.SetUpShopSuccess:
            return {
                ...state,
                data: action.payload,
                loading: false,
                loaded: true,
                isError: false,
                error: undefined
            };
        case ZenIndiaShoppeActionTypes.SetUpShopFailure:
            return {
                ...state,
                loading: false,
                loaded: true,
                isError: true,
                error: action.payload
            };
        default:
            return state;
    }
}