import { createFeatureSelector, createSelector } from "@ngrx/store";
import { IShopSettingsState, ZenIndiaShoppeFeatureKey } from "./zen-india-shoppe.reducer";

export const getShopDetailsState = createFeatureSelector<IShopSettingsState>(ZenIndiaShoppeFeatureKey);

export const getShopDetails = createSelector(
    getShopDetailsState,
    (udState) => {
        if (!udState) {
            return null;
        }
        return udState.data;
    }
);

export const isShopDetailsLoaded = createSelector(
    getShopDetailsState,
    (udState) => {
        if (!udState) {
            return null;
        }
        return udState.loaded;
    }
);

export const isShopDetailsError = createSelector(
    getShopDetailsState,
    (udState) => {
        if (!udState) {
            return null;
        }
        return udState.error;
    }
);