import { ViewportScroller } from '@angular/common';
import { Component, HostListener } from '@angular/core';

@Component({
  selector: 'zen-shoppe-zen-shoppe-back-to-top',
  templateUrl: './zen-shoppe-back-to-top.component.html',
  styleUrls: ['./zen-shoppe-back-to-top.component.scss'],
})
export class ZenShoppeBackToTopComponent {

  displayBackToTop = false;

  constructor(private scroll: ViewportScroller) { }

  @HostListener('window:scroll', ['$event'])
  onScroll(event: any) {
    this.displayBackToTop = true;
  }

  scrollToTop() {
    // To do: Animate scroll to top
    this.scroll.scrollToPosition([0, 0]);
  }
}
