// Angular
import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

// Modules
import { ZenIndiaShoppeRoutingModule } from './zen-india-shoppe.routing.module';
import { SharedModule } from '@zen-shoppe/shared';

// Components
import { ZenIndiaShoppeHomeComponent } from './Containers/ZenIndiaShoppeHome/zen-india-shoppe-home.component';
import { ZenIndiaShoppeShopComponent } from './Containers/ZenIndiaShoppeShop/zen-india-shoppe-shop.component';
import { ZenIndiaShoppeProductComponent } from './Containers/ZenIndiaShoppeProduct/zen-india-shoppe-product.component';
import { ZenIndiaShoppeContactUsComponent } from './Containers/ZenIndiaShoppeContactUs/zen-india-shoppe-contact-us.component';
import { ZenIndiaShoppeFaqsComponent } from './Containers/ZenIndiaShoppeFAQs/zen-india-shoppe-faqs.component';
import { StoreModule } from '@ngrx/store';
import { ZenIndiaShoppeEffects, ZenIndiaShoppeFeatureKey, ZenIndiaShoppeReducer } from './State';
import { ZenIndiaShoppeRootComponent } from './Containers/ZenIndiaShoppeRoot/zen-india-shoppe-root.component';
import { EffectsModule } from '@ngrx/effects';
import { IShopSettingsConfiguration } from './Models/Interfaces/shop-settings-configurations.interface';
import { shopSettingsConf } from './Models/Constants/data-providers.const';

@NgModule({
  imports: [
    ZenIndiaShoppeRoutingModule,
    SharedModule,
    CommonModule,
    StoreModule.forFeature(ZenIndiaShoppeFeatureKey, ZenIndiaShoppeReducer),
    EffectsModule.forFeature(ZenIndiaShoppeEffects)
  ],
  declarations: [
    ZenIndiaShoppeRootComponent,
    ZenIndiaShoppeHomeComponent,
    ZenIndiaShoppeShopComponent,
    ZenIndiaShoppeProductComponent,
    ZenIndiaShoppeContactUsComponent,
    ZenIndiaShoppeFaqsComponent
  ],
})
export class ZenIndiaShoppeModule {
  static forRoot(envconf: IShopSettingsConfiguration): ModuleWithProviders<ZenIndiaShoppeModule> {
    return {
      ngModule: ZenIndiaShoppeModule, providers:
        [
          { provide: shopSettingsConf, useValue: envconf }
        ]
    };
  }
 }
