import { Action } from '@ngrx/store';
import { IShopSettings } from '../../Models/Interfaces/shop-settings.interface';

export enum ZenIndiaShoppeActionTypes {
    SetUpShop = '[ZEN INDIA SHOPPE] SET UP SHOP',
    SetUpShopSuccess = '[ZEN INDIA SHOPPE] SET UP SHOP SUCCESS',
    SetUpShopFailure = '[ZEN INDIA SHOPPE] SET UP SHOP FAILURE',
}

export class SetUpShop implements Action {
    readonly type = ZenIndiaShoppeActionTypes.SetUpShop;
    constructor() { }
}

export class SetUpShopSuccess implements Action {
    readonly type = ZenIndiaShoppeActionTypes.SetUpShopSuccess;
    constructor(public payload: IShopSettings) { }
}

export class SetUpShopFailure implements Action {
    readonly type = ZenIndiaShoppeActionTypes.SetUpShopFailure;
    constructor(public payload: any) { }
}


export type ZenIndiaShoppeActions = SetUpShop
    | SetUpShopSuccess
    | SetUpShopFailure;