import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ZenIndiaShoppeHomeComponent } from './Containers/ZenIndiaShoppeHome/zen-india-shoppe-home.component';
import { ZenIndiaShoppeShopComponent } from './Containers/ZenIndiaShoppeShop/zen-india-shoppe-shop.component';
import { ZenIndiaShoppeProductComponent } from './Containers/ZenIndiaShoppeProduct/zen-india-shoppe-product.component';
import { ZenIndiaShoppeContactUsComponent } from './Containers/ZenIndiaShoppeContactUs/zen-india-shoppe-contact-us.component';
import { ZenIndiaShoppeFaqsComponent } from './Containers/ZenIndiaShoppeFAQs/zen-india-shoppe-faqs.component';
import { ZenIndiaShoppeRootComponent } from './Containers/ZenIndiaShoppeRoot/zen-india-shoppe-root.component';

const ZEN_INDIA_SHOPPE_ROUTES: Routes = [
    {
        path: '',
        component: ZenIndiaShoppeRootComponent,
        children: [
            {
                path: '',
                component: ZenIndiaShoppeHomeComponent
            },
            {
                path: 'shop',
                component: ZenIndiaShoppeShopComponent
            },
            {
                path: 'product',
                component: ZenIndiaShoppeProductComponent
            },
            {
                path: 'contact',
                component: ZenIndiaShoppeContactUsComponent
            },
            {
                path: 'faq',
                component: ZenIndiaShoppeFaqsComponent
            }
        ]
    },
    {
        path: '**',
        redirectTo: ''
    }
];

@NgModule({
    imports: [RouterModule.forChild(ZEN_INDIA_SHOPPE_ROUTES)],
    exports: [RouterModule]
})
export class ZenIndiaShoppeRoutingModule { }
