import { ApplicationConfig, importProvidersFrom } from '@angular/core';
import {
  provideRouter,
  withEnabledBlockingInitialNavigation,
  withHashLocation,
} from '@angular/router';
import { APP_ZEN_INDIA_SHOPPE_ROUTES } from './app.routes';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { HttpClientModule } from '@angular/common/http';
import { ZenIndiaShoppeModule } from '@zen-shoppe/zen-india-shoppe';
import { environment } from '../environments/environment';

export const appConfig: ApplicationConfig = {
  providers: [importProvidersFrom([BrowserAnimationsModule],
    HttpClientModule,
    ZenIndiaShoppeModule.forRoot(environment),
    StoreModule.forRoot({}),
    EffectsModule.forRoot([]),
    StoreDevtoolsModule.instrument({
      maxAge: 25, // Retains last 25 states
      logOnly: false // Restrict extension to log-only mode
    })
  ),
  provideRouter(APP_ZEN_INDIA_SHOPPE_ROUTES, withEnabledBlockingInitialNavigation())],
};
