import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { shopSettingsConf } from '../../Models/Constants/data-providers.const';
import { IShopSettingsConfiguration } from '../../Models/Interfaces/shop-settings-configurations.interface';
import { IShopSettings } from '../../Models';

@Injectable({
  providedIn: 'root'
})
export class SetupShopService {
  private shopSettingsConf: IShopSettingsConfiguration;

  constructor(private http: HttpClient, @Inject(shopSettingsConf) _shopSettingsConf: IShopSettingsConfiguration,) {
    this.shopSettingsConf = _shopSettingsConf;
  }

  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Accept-Type': 'application/json',
    })
  };

  setUpShop() {
    const userEndPoint = this.shopSettingsConf.baseUrl + this.shopSettingsConf.settingsEndPoint + "settings.json";
    return this.http.get<IShopSettings>(userEndPoint, this.httpOptions);
  }
}
