import { Component, Input } from '@angular/core';
import { CheckboxChangeEvent } from 'primeng/checkbox';

@Component({
  selector: 'zen-shoppe-zen-shoppe-shop-filter',
  templateUrl: './zen-shoppe-shop-filter.component.html',
  styleUrls: ['./zen-shoppe-shop-filter.component.scss'],
})
export class ZenShoppeShopFilterComponent {
  @Input()
  title!: string;
  @Input()
  filterParams!: any[];

  checkChanged(event: CheckboxChangeEvent, filter: any) {
  }

  childCheckChanged(event: CheckboxChangeEvent, subFilter: any, filter: any) {
    filter.indeterminate = true;
  }
}
