// ngrx
import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { catchError, switchMap } from 'rxjs';
import { SetupShopService } from '../../Services/setup-shop/setup-shop.service';
import { SetUpShopFailure, SetUpShopSuccess, ZenIndiaShoppeActionTypes } from './zen-india-shoppe.actions';

@Injectable()
export class ZenIndiaShoppeEffects {
    constructor(
        private actions$: Actions,
        private setupShopService: SetupShopService
    ) { }

    setupShop$ = createEffect(() => this.actions$.pipe(
        ofType(ZenIndiaShoppeActionTypes.SetUpShop),
        switchMap(() => {
            return this.setupShopService.setUpShop();
        }),
        switchMap((response) => {
            if (response) {
                return [new SetUpShopSuccess(response)]
            }
            return [new SetUpShopFailure('Error: setupShop$ : Api Call failed')];
        }),
        catchError((err) => {
            return [
                new SetUpShopFailure(err)
            ];
        })
    ));
}
